import nodata from '@/components/noData'

export default {
  data () {
    return {
      countDown: 3,
      noData: {
        txt: '啊哦~ 你所访问的页面不存在，3s后返回主页',
        img: 'static/images/404.png'
      }
    }
  },
  created () {
    const that = this
    const interval = setInterval(function () {
      if (that.countDown <= 1) {
        clearInterval(interval)
        that.countDown = 3
        that.$router.replace({path: '/home'})
      } else {
        that.countDown--
        that.noData.txt = '啊哦~ 你所访问的页面不存在，' + that.countDown + 's后返回主页'
      }
    }, 1000)
  },
  components: {
    nodata
  },
  methods: {

  }
}



